import axios from "axios";
import { useEffect, useState } from "react";
import Cart from "./Cart";
function findProduct(id) {
  let find = { find: false, index: 0 };
  const cartItem = JSON.parse(sessionStorage.getItem("myShop"));
  for (let i = 0; i < cartItem.length; i++) {
    if (cartItem[i].id === id) {
      find = { find: true, index: i };
      i = cartItem.length;
    }
  }
  return find;
}

export default function Home() {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [update, setUpdate] = useState("");
  useEffect(() => {
    axios.get("https://fakestoreapi.com/products").then((res) => {
      setData(res.data);
      setLoading(true);
    });
  });

  function addToCart(product) {
    const cartItem = JSON.parse(sessionStorage.getItem("myShop"));
    console.log(findProduct(product.id).find);
    if (findProduct(product.id).find) {
      cartItem[findProduct(product.id).index] = {
        id: product.id,
        qty: cartItem[findProduct(product.id).index].qty + 1
      };
      setUpdate(Math.random());
    } else {
      cartItem.push({ id: product.id, qty: 1 });
      setUpdate(Math.random());
    }
    sessionStorage["myShop"] = JSON.stringify(cartItem);
  }

  return (
    <div>
      <Cart update={update} />
      {loading ? (
        data.map((product, index) => {
          return (
            <div key={index} style={{ marginBottom: "5vh" }}>
              <img
                src={product.image}
                alt="..."
                style={{ height: "20vh", width: "20vh" }}
              />
              <p>{product.description}</p>
              <button
                type="button"
                style={{ padding: "1vh", fontSize: "3vh" }}
                onClick={() => addToCart(product)}
              >
                Add to cart
              </button>
            </div>
          );
        })
      ) : (
        <div>Loading...</div>
      )}
    </div>
  );
}
